
#app2 .tab-bar{
 display: flex;
}
#app2 .tab-bar > li {
    border: 1px solid gray;
    width: 50%;
}
#app2 .tab-bar > li.selected {
    background: yellowgreen;
    color: cornsilk;
}

#app2 .tab-content > li {
    display: none;
}

#app2 .tab-content > li.active{
    display: block;
}